<template>
  <div class="article-add">
    <van-nav-bar title="添加文章" left-text="返回" left-arrow right-text="提交" @click-left="onClickLeft" @click-right="onClickRight"/>
    <quill-editor v-model="content"
                  ref="myQuillEditor"
                  :options="editorOption"
                  @blur="onEditorBlur($event)"
                  @focus="onEditorFocus($event)"
                  @ready="onEditorReady($event)">
    </quill-editor>

    <van-popup v-model="showPreview">
        {{content}}
    </van-popup>
  </div>
</template>
<script>
import {NavBar,Popup} from 'vant'
export default {
  components: {
    [NavBar.name]: NavBar,
    [Popup.name]: Popup
  },
  data() {
    return {
      content: '',
      editorOption: {
        // some quill options
      },
      showPreview: false
    }
  },
  methods: {
    onEditorBlur(quill) {
      console.log('editor blur!', quill)
    },
    onEditorFocus(quill) {
      console.log('editor focus!', quill)
    },
    onEditorReady(quill) {
      console.log('editor ready!', quill)
    },
    onEditorChange({quill, html, text}) {
      console.log('editor change!', quill, html, text)
      this.content = html
    },
    onClickLeft(){
      this.$router.back()
    },
    onClickRight(){
      this.showPreview = true
    }
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill
    },
  },
  mounted() {
    console.log('this is current quill instance object', this.editor)
  },
}
</script>
<style>
.article-add .quill-editor{
  height: calc(100% - 50px);
}
.article-add .ql-container{
  height: calc(100% - 114px);
}
</style>



